window.Webflow ||= [];
window.Webflow.push(() => {
  // Query the elements
  const form = document.querySelector<HTMLFormElement>('[fs-element="form"]');

  const resultNewClientYearlyTotal = document.querySelector<HTMLFormElement>(
    '[fs-element="result-new-client-yearly-total"]'
  );
  const resultVIPYearlyTotal = document.querySelector<HTMLFormElement>(
    '[fs-element="result-vip-yearly-total"]'
  );
  const resultSupplementYearlyTotal = document.querySelector<HTMLFormElement>(
    '[fs-element="result-supplement-yearly-total"]'
  );
  const resultCoachReferralYearlyTotal = document.querySelector<HTMLFormElement>(
    '[fs-element="result-coach-referral-yearly-total"]'
  );
  const resultYearlyTotal = document.querySelector<HTMLFormElement>(
    '[fs-element="result-overall-yearly-total"]'
  );
  const seeMoreLink = document.getElementById('see-breakdown-link').parentElement;
  const breakdownChart = document.getElementById('breakdown-chart');
  if (breakdownChart) { breakdownChart.classList.add('table-responsive-lg'); }
  let breakdownChartHTML = '';

  const resetValues = () => {
    //document.getElementById('breakdown-chart').style.display = 'none';
    resultNewClientYearlyTotal.textContent = '';
    resultVIPYearlyTotal.textContent = '';
    resultSupplementYearlyTotal.textContent = '';
    resultCoachReferralYearlyTotal.textContent = '';
    resultYearlyTotal.textContent = '';
    breakdownChartHTML = '';
    breakdownChart.innerHTML = breakdownChartHTML;
    //breakdownChart.innerHTML = '';
  }

  if (
    !form ||
    !resultNewClientYearlyTotal ||
    !resultVIPYearlyTotal ||
    !resultSupplementYearlyTotal ||
    !resultCoachReferralYearlyTotal ||
    !seeMoreLink ||
    !breakdownChart
  )
    return;

  // Listen for See Breakoown link click
  seeMoreLink.addEventListener('click', (e) => {
    e.preventDefault(); // Prevents the default behavior of the link, which is navigating to the href
    breakdownChart.innerHTML = breakdownChartHTML;
    // Your custom code or function here
    console.log('Link clicked!');
  });

  // Listen for form submission events
  form.addEventListener('submit', (e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log('here');
    // Get the data from the calculator
    const formData = new FormData(form);
    const amount = formData.get('amount-dd');

    if (!amount) return;

    // Perform any math calculations
    // Get the input value
  let numClientsReferredPerMonth = parseInt(amount);

  // Verify if the input is a valid number within the specified range
  if (isNaN(numClientsReferredPerMonth) || numClientsReferredPerMonth <= 0 || numClientsReferredPerMonth >= 101) {
    //alert('Please choose a value between 1 and 100.');
    resetValues();
    return;
  }

  // Perform the calculations
  const newClientPrice = 99.5;
  const vipMonthlyPrice = 34.5;
  const vipConversionRate = 0.8;
  const vipChurnRate = 0.07;
  const supplementMonthlyPrice = 17;
  const supplementsPerMonth = 1;
  const coachReferralPrice = 1000;
  const numMonths = 12;
  
  let numCoachesReferred = (numClientsReferredPerMonth > 10 ? 11 : 4);
  
  let newClientTotalRevenue = 0;
  let vipClientTotalRevenue = 0;
  let supplementTotalRevenue = 0;
  let coachReferralTotalRevenue = 0;
  
  // Format the price above to USD using the locale, style, and currency.
  const USDollar = new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
  });
  
  let currentVIPClients = 0;
  let totalRevenue = 0;
  
  let tableOut = '<table class="table"><tr>';
  tableOut += '<th>Month</th>';
  tableOut += '<th>New&nbsp;Clients<br><span class="smtitle">per month</span></th>';
  tableOut += '<th>New&nbsp;Client Revenue<br><span class="smtitle">new clients * $99.50</span></th>';
  tableOut += '<th>VIP&nbsp;Clients<br><span class="smtitle">80% of new clients - 7% churn</span></th>';
  tableOut += '<th>Churned<br><span class="smtitle">7% of VIP</span></th>';
  tableOut += '<th>VIP Client Revenue<br><span class="smtitle">VIP clients * $34.50</span></th>';
  tableOut += '<th>Supplement<br><span class="smtitle">subs per month</span></th>';
  tableOut += '<th>Supplement Revenue<br><span class="smtitle">subs * $17.00</span></th>';
  tableOut += '</tr>';

  for (let month = 0; month < numMonths; month++) {
  	tableOut += '<tr>';

    let vipClientsForMonth = 0;
    let churnedUsers = 0;
    if (month > 0) {
    	vipClientsForMonth = vipConversionRate * numClientsReferredPerMonth;
      currentVIPClients += vipClientsForMonth;
      if (month > 1) {
        churnedUsers = vipChurnRate * currentVIPClients;
        currentVIPClients -= churnedUsers;
      }
    }
    let newClientRevenue = numClientsReferredPerMonth * newClientPrice;
    let vipClientRevenue = currentVIPClients * vipMonthlyPrice;
    totalRevenue += newClientRevenue + vipClientRevenue;
    newClientTotalRevenue += newClientRevenue;
    vipClientTotalRevenue += vipClientRevenue;
    
    let supplementMonthlyRevenue = supplementsPerMonth * (month+1) * supplementMonthlyPrice;
    supplementTotalRevenue += supplementMonthlyRevenue;
    
    tableOut += '<td>' + Intl.DateTimeFormat('en', { month: 'short' }).format(new Date(0, month)) + '</td>';
    tableOut += '<td>'+numClientsReferredPerMonth.toFixed(0)+'</td>';
    tableOut += '<td>'+USDollar.format(newClientRevenue)+'</td>';
    tableOut += '<td>'+currentVIPClients.toFixed(2)+'</td>';
    tableOut += '<td>'+churnedUsers.toFixed(2)+'</td>';
    tableOut += '<td>'+USDollar.format(vipClientRevenue)+'</td>';
    tableOut += '<td>'+supplementsPerMonth.toFixed(0)+'</td>';
    tableOut += '<td>$'+supplementMonthlyRevenue.toFixed(0)+'</td>';
    tableOut += '</tr>';
  }
  
  // Supplements
  totalRevenue += supplementTotalRevenue;
  
  // Coach Referrals
  coachReferralTotalRevenue = coachReferralPrice * numCoachesReferred;
  totalRevenue += coachReferralTotalRevenue;

  //Totals row
  tableOut += '<tr class="totals_row">';
  tableOut += '<td></td>';
  tableOut += '<td>'+(numClientsReferredPerMonth*numMonths).toFixed(0)+'</td>';
  tableOut += '<td>'+USDollar.format(newClientTotalRevenue)+'</td>';
  tableOut += '<td></td>';
  tableOut += '<td></td>';
  tableOut += '<td>'+USDollar.format(vipClientTotalRevenue)+'</td>';
  tableOut += '<td>'+(supplementsPerMonth*numMonths).toFixed(0)+'</td>';
  tableOut += '<td>'+USDollar.format(supplementTotalRevenue)+'</td>';
  tableOut += '</tr>';
	tableOut += '</table>';
  
  breakdownChartHTML = tableOut;

  // Display the output
  resultYearlyTotal.textContent = totalRevenue.toLocaleString('en', { maximumFractionDigits: 0 });
  resultNewClientYearlyTotal.textContent = newClientTotalRevenue.toLocaleString('en', { maximumFractionDigits: 0 });
  resultVIPYearlyTotal.textContent = vipClientTotalRevenue.toLocaleString('en', { maximumFractionDigits: 0 });
  resultSupplementYearlyTotal.textContent = supplementTotalRevenue.toLocaleString('en', { maximumFractionDigits: 0 });
  resultCoachReferralYearlyTotal.textContent = coachReferralTotalRevenue.toLocaleString('en', { maximumFractionDigits: 0 });
  document.getElementById('breakdown-chart').innerHTML = '';
    
  });
});